.account-menu-item {
    margin-bottom: 16px;
    a {
        display: flex;
        align-items: center;
        border: solid 1px $c-border;
        min-height: 96px;
        border-radius: 5px;
        padding: 0 20px;
        background-color: #fff;
        @include transition();
        .img {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 40px;
            flex: 0 0 40px;
        }
        .content {
            flex: auto;
            padding-left: 20px;

            .t {
                font-family: $font2;
                font-size: 18px;
                font-weight: 600;
                margin-bottom: 5px;
                color: $c-brand;
            }
            .v {
                font-size: 12px;
            }
        }

        &:hover {
            box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16);
        }
    }
}

.ord-header {
    .ord-search {
        padding-bottom: 20px;
        .f-input {
            padding: 0;
            input {
                font-size: 12px;
            }
    
            .btn-search {
                width: 30px;
                height: 100%;
                .icon {
                    width: 14px;
                    height: 14px;
                }
            }
        }
    }
}

.ord-accordion {
    > li {

    }
    .acc-item {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        padding: 20px;
        overflow-x: auto;
        > .item {
            flex: 0 0 150px;
            padding: 0 5px;
        }
        > button {
            width: auto;
            flex: 0 0 50px;
        }

        .link-inline {
            color: $c-brand;
            text-decoration: none;
        }
    }

    .acc-body {
        .right {
           margin-top: 40px;
        }
    }
}

.purchase-accordion {
    > li {
        margin-bottom: 16px;
    }
    .acc-item {
        width: 100%;
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        overflow-x: auto;
        &-top  {
            font-family: $font2;
            font-size: 18px;
            font-weight: 500;
            padding: 24px 20px 10px;
        }
        &-content {
            display: flex;
            padding: 0 20px 20px;
            .item {
                margin-right: 20px;
                &:last-child {
                    margin-right: 0;
                }
            }
        }

        &-bottom {
            position: relative;
            .progress-container {
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
            }
        }

       
        > button {
            width: 100%;
            padding: 0;
        }
    }
}

.ord-item {
    margin-bottom: 16px;
    &:last-child {
        margin-bottom: 0;
    }
    a {
        display: flex;
        align-items: center;
    }
    &-img {
        flex: 0 0 30%;
    }

    &-content {
        flex: auto;
        padding: 0 10px;
    }

    .ord-title {
        font-family: $font2;
        font-size: 16px;
        font-weight: 600;
        height: 40px;
        line-height: 20px;
        @include line-clamp(2);
        margin-bottom: 8px;
    }

    .ord-price {
        font-size: 16px;
        .prc-first {
            margin-right: 5px;
            font-size: 14px;
        }
    }
}
.btn-user.activated {
    box-shadow: none !important;
}

#account_popup {
    padding: 20px;
    top: calc(100% + 20px);
    filter: drop-shadow(0px -5px 16px rgba(0, 0, 0, 0.16));
    left: 50%;
    transform: translateX(-50%);
    &:before {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: -10px;
        border-style: solid;
        border-width: 0 10px 10px 10px;
        border-color: transparent transparent #fff transparent;
    }
    a {
        display: block;
        text-align: center;
        margin-bottom: 16px;
        &:last-child {
            margin-bottom: 0;
        }

        &:hover {
            text-decoration: underline;
        }
    }
}