.footer-top {
    padding: 70px 0;
}


.footer-menu {
    &-item {
        margin-bottom: 0;
    }
}

.footer-bottom {
    background-color: #fff;
    padding: 30px 0;
    .content {
        display: flex;
        .left {
            flex: 1;
        }
        .center {
            flex: auto;
        }
        .right {
            flex: 1;
        }
    }
}

.footer-contact {
    margin-bottom: 0;
}

.footer-logo {
    margin-bottom: 0;
    text-align: left;
}