#main-slider {
    margin-bottom: 20px;
    height: 515px;
    border-radius: 10px;
    overflow: hidden;

    .banner {
        position: relative;
        height: 100%;
    }

    .bnr-media{
        transition: none;
        bottom: 30px;
        picture{
            transition: transform 1s $transition-ease-out, opacity .666s linear;
            transform: translateY(50px);
            opacity: 0;
        }
    }

    .bnr-body {
        padding: 50px 30px 30px;
        transition: transform 1s $transition-ease-out, opacity .666s linear;
        transform: translateY(20px);
        opacity: 0;
        height: auto;
    }
    
    .bnr-button{
        @include transitions($func: 'eut', $dur: 'slow', $delay: .333s);
        transform: translateY(20px);
        opacity: 0;
    }

    .bnr-product-list {
        position: absolute;
        right: 60px;
        top: 80px;
        z-index: 4;
        max-width: 340px;
        width: 100%;
        > * {
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    .swiper-button-prev {
        left: 16px;
    }

    .swiper-button-next {
        right: 16px;
    }

    .swiper-wrapper {
        .swiper-slide:first-child {
            .bnr-media {
                picture{
                    transform: none;
                    opacity: 1;
                }
            }
            .bnr-body {
                transform: none;
                opacity: 1;
            }
            .bnr-button {
                transform: none;
                transition: none;
                opacity: 1;
            }
        }
    }

    .swiper-slide-active {
        .bnr-media {
            picture {
                transform: translateY(0);
                opacity: 1;
            }
        }
        .bnr-body {
            transform: translateY(0);
            opacity: 1;
        }
        .bnr-button {
            transform: translateY(0);
            opacity: 1;
        }
    }

    .swiper-pagination {
        position: absolute;
        bottom: 16px;
        left: 50%;
        z-index: $z-lower;
        transform: translateX(-50%);
    }

    .bnr-full-img {
        .bnr-media {
            bottom: 0;
        }
        picture {
            position: relative;
            height: 100%;
            img {
                position: absolute;
                left: 0;
                bottom: 0;
                top: 0;
                right: 0;
                margin: auto;
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
        }
    }
}

.quick-account-list  {
    margin-bottom: 30px;
}

.cta-full-item {
    background-color: #AD2123;
    padding: 20px;
    border-radius: 5px;
    margin-bottom: 50px;
    .content {
        text-align: center;
        color: #fff;
        .t {
            font-size: 20px;
            font-weight: 600;
            font-family: $font2;
        }
        .v {
            font-size: 12px;
            max-width: 376px;
            margin: 0 auto;
        }
        .b {
            margin-top: 30px;
        }
    }
}

.cta-product-item {
    position: relative;
    margin-bottom: 50px;
    .content {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 4;
        padding: 20px;
        padding-right: 0;
        width: 100%;
        .text {
            color: #fff;
            padding-right: 20px;
            .t {
                font-size: 26px;
                font-weight: 600;
                font-family: $font2;
                margin-bottom: 5px;
            }
            .v {
                font-size: 18px;
                max-width: 410px;
            }
        }
    }

    > .img {
        picture {
            position: relative;
            height: 655px;
            img {
                position: absolute;
                left: 0;
                bottom: 0;
                top: 0;
                right: 0;
                margin: auto;
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
        }
    }

    .cta-product-list {
        display: flex;
        flex-wrap: nowrap;
        overflow-x: auto;
        padding-bottom: 10px;
        margin-top: 20px;
        > * {
            margin-right: 16px;
            flex: 0 0 35vw;
        }
    }
}