// Popup Base
.modal{
    &-dialog{
        min-height: auto;
        height: 100vh;    
    }

    &-header{
        margin: 0 16px;
        padding: 20px 0;
    }

    &-body{
        padding: 30px 16px;
    }

    &-footer{
        padding: 8px 16px;
    }

    &-general {
        .modal-body {
            padding: 0 16px;
        }
    }
}
.popup {
    &-lg {
        height: 666px;
    }
}