// mobile elements
#main-menu .btn-close,
.menu-user,
.menu-search,
.menu-links,
.menu-social-links,
.menu-category > li > a::after,
#main-menu li.all {
    display: none !important;
}

// header
#site-header {
    &::before {
        height: $header-height-desktop;
    }
}

@include header-appshell-desktop;

%d-header-modals {
    .header-top {
        background: $header-top-bg;
        color: $header-top-color;
        transition: none;

        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        z-index: 7;
    }
    .logo img {
        animation: none !important;
    }
    .logo-org {
        display: inline;
    }
    .logo-alt {
        display: none;
    }
    #overlay {
        display: block;
    }
}

// menu
$d-menu-space-x: 20px;
#main-menu {
    position: relative;
    top: -10px;
    height: auto;
    background-color: $menu-lv-1-border-color;
    overflow: visible;
    padding: 0 $d-menu-space-x;
    li {
        > .sub {
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            background: $c-white;
            padding: 25px 0;
            align-items: flex-start;
            justify-content: center;
            max-height: calc(100vh - 290px);
            overflow: auto;
        }
        &.selected {
            background: none;
            > a {
                font-weight: 400;
            }
            > .sub {
                flex-direction: row;
            }
        }
    }
}

.menu-category {
    $p: 10px;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid $c-grey-lighter;
    animation: none;
    > li {
        width: 100px + ($p * 2);
        padding: 20px $p 0;
        opacity: 1;
        animation: none;
        > a {
            flex-direction: column;
            text-align: center;
            padding: 0 0 15px;
            border-bottom: 3px solid transparent;
            top: 1px;
            width: 100%;
            height: 100%;
            > .responsive {
                display: inline-block;
                margin-bottom: 5px;
            }
        }
        &.selected {
            > a {
                border-color: $c-primary;
                &:after {
                    display: none;
                }
            }
        }
    }
    // lv 2
    .lv-2 {
        display: block;
        column-count: 3;
        column-gap: 0;
        padding: 0;
        margin: 0 auto;
        width: 700px;
        > li {
            width: auto;
            padding: 5px 30px;
            break-inside: avoid;
            > a {
                display: inline-flex !important;
                flex-direction: row;
                text-align: left;
                .responsive {
                    display: none;
                }
                > span {
                    margin-top: 0;
                    @include font-set(h-xsmall);
                }
            }
        }
        .sublv{
            .banner{
                display: none;
            }
        }
    }
    // lv 3
    .lv-3 {
        display: block;
        .responsive {
            display: none;
        }
    }
    // banner
    .banner {
        flex: none;
        order: 1;
        width: 375px;
        margin: 5px 120px 0 0;
    }
}

.menu-brands {
    $p: 15px;
    flex: 1;
    justify-content: center;
    padding: 0;
    > li {
        max-width: 115px + ($p * 2);
        padding: 0 $p;
    }
}

.menu-ready {
    @extend %d-header-modals;
    #main-menu {
        animation: none;
    }
    &.page-type-cover {
        .header-top {
            color: $header-top-opaque-color;
            .col-left, .col-right{
                a:not(.btn-outline-dark), button:not(.btn-outline-dark), .icon, span{
                    color: $header-top-opaque-color;
                }
            }
        }
    }
}

// search
$c: 964px + 32px;
#header-search {
    position: relative;
}

.search-input {
    padding: 50px 0 40px;
    width: 100%;
    max-width: $c;
    margin: auto;
}

.suggested-results {
    padding: 10px;
    max-width: $c + 20px;
    margin: 0 auto;
    
    > .list {
        width: 100%;
        > div {
            flex: 0 0 calc(100% / 6);
            .prd-mini {
                border-color: $c-border
            }
        }
    }
}

[class*="search-ready"] {
    @extend %d-header-modals;

    &.page-type-cover {
        .header-top {
            color: $header-top-opaque-color;
            .col-left, .col-right{
                a:not(.btn-outline-dark), span, button:not(.btn-outline-dark), .icon {
                    color: $header-top-opaque-color;
                }
            }
        }
    }
}

// Notifiers
#notifiers{
    left: -46px;
    top: $header-height-desktop - 14px;
}

#cart-notifier{
    width: 360px;
}

// Hidden Header
.m-header-hidden{
    #site-header{
        display: block;
    }
}

// Header User Menu
.hum{
    &:hover{
        .hum-body{
            display: block;
        }
    }
}

.menu-quick-links {
    display: none;
}