.compare-info {
    display: none;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: $z-normal;
    width: 100%;
    height: 60px;
    padding: 0 $container-padding-x;
    animation: toTop .32s ease;
    background: $c-white;

    .btn-compare {
        padding: 0 30px 0 40px;
        .qty-badge {
            background: $c-white;
            color: $c-black;
        }
    }
    
    .compare-activated & {
        display: flex;
    }
}

.top-panel {
    background-color: $c-light;
    padding: 14px;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    > .left {
        flex: 0 0 auto;
    }
    > .center {
        flex-basis: 0;
        flex-grow: 1;
        text-align: right;
        h1 {
            margin-bottom: 0;
            font-size: 16px;
            @include line-clamp(1)
        }
    }
    > .right {
        flex: 0 0 auto;
        margin-left: auto;
    }
}

.plp-filter-content {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 16px;
    .record-count {
        margin-right: 10px;
    }

    .btn-list {
        display: flex;
        margin-left: auto;;
        .btn-filter {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 10px;
            background-color: #fff;
            border-radius: 5px;
            height: 30px;
            padding: 0 8px;
            min-width: 110px;
            border: solid 1px $c-dark;
            > a,button {
                height: 100%;
                width: 100%;
            }
            svg {
                width: 12px;
                height: 100%;
                max-height: 12px;
                margin-left: 5px;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }

    .filter-sorting {
        flex: 0 0 100%;
        margin-bottom: 16px;
        display: flex;
        justify-content: flex-end;
    }
}

#sorting_popup {
    padding: 20px;
    top: calc(100% + 20px);
    &:before {
        content: '';
        position: absolute;
        left: 40px;
        top: -10px;
        border-style: solid;
        border-width: 0 10px 10px 10px;
        border-color: transparent transparent #fff transparent;
    }
    .form-row {
        margin-bottom: 16px;
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.plp-sub-categories {
    margin-bottom: 20px;
    padding-left: 16px;
    ul {
        display: flex;
        flex-wrap: nowrap;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        scroll-snap-type: x mandatory;
        padding-bottom: 16px;
        li {
            scroll-snap-align: start;
            margin-left: 8px;
            flex: 0 0 calc(100vw / 1.7);
            a {
                display: flex;
                align-items: center;
                background-color: #FFE5E4;
                text-align: center;
                padding: 5px;
                font-size: 16px;
                text-align: left;
                border-radius: 5px;
                overflow: hidden;
                @include transition();

                figure {
                    margin-right: 5px;
                    width: 82px;
                    height: 82px;
                    &:before {
                        display: none;
                    }
                    img {
                        width: auto;
                        height: auto;
                        max-width: 100%;
                        max-height: 100%;
                        margin: auto;
                        inset: 0;
                    }
                }

                &:hover {
                    transform: scale(1.02);
                    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16);
                }
            }

            &:first-child {
                margin-left: 0;
            }
        }
    }
}

.products { 
    display: flex;
    flex-wrap: wrap;
    margin: 0 -4px;
    > .prd {
        flex: 0 0 50%;
        max-width: 50%;
        padding: 0 4px;
        margin-bottom: 8px;
    }
}

body.page-product-detail {
    .top-panel {
        margin-bottom: 0;
        > .left,.center {
            display: none;
        }

        > .right {
            margin-left: 0;
            flex: 0 0 100%;
        }
    }

    .top-panel-fixed {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        max-width: none;
        z-index: 2;
        background-color: #fff;
        box-shadow: 0px -5px 10px rgba(0, 0, 0, 0.08);
        padding: 0 !important;
        .top-panel {
            background-color: #fff;
        }
        
    }
}

.pdp {
    &-top-shopping {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &-top-price {
        margin-right: 30px;
        font-size: 18px;
        font-weight: 600;
    }

    &-top-container {
        margin-bottom: 30px;
    }

    &-image {
        &-item {
            a {
                display: block;
                position: relative;
                padding-top: 100%;
                overflow: hidden;
                img {
                    position: absolute;
                    left: 0;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    max-width: 100%;
                    max-height: 100%;
                    margin: auto;
                }
            }
        }
    }

    &-thumb-image {
        display: none;
    }

    &-main-section {
        display: flex;
        border-bottom: solid 1px $c-border;
        padding-bottom: 8px;
        margin-bottom: 20px;
        .left {
            flex: 1;
            padding-right: 10px;
        }
    
        .right {
            margin-left: auto;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    
        .prd-compare {
            margin-left: auto;
        }
    }

    &-energy {
        margin-bottom: 5px;
    }
    &-name {
        margin-bottom: 12px;
        h1 {
            font-size: 18px;
            font-weight: 600;
        }
        a {
            font-size: 14px;
            @include transitions();
            &:hover {
                color: $c-brand;
            }
        }
    }

    &-labels {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -5px 16px;
        > div {
            padding: 0 5px;
            margin-bottom: 10px;
        }
    
        .prd-txt-label {
            display: flex !important;
            height: 26px;
            font-size: 12px;
            background-color: $c-light;
        }
    }

    &-options {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -5px 20px;
        > div {
            padding: 0 5px;
            margin-bottom: 10px;
        }

        .opt-item {
            position: relative;
            width: 62px;
            height: 62px;
            display: block;
            border: 1px solid $c-border;
            border-radius: 5px;
            overflow: hidden;

            img {
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                right: 0;
                max-width: 100%;
                max-height: 100%;
                margin: auto;
            }

            &.selected {
                border-color: $c-brand;
            }
        }
    }

    &-features-list {
        margin-bottom: 30px;
        .item {
            display: flex;
            margin-bottom: 16px;
            .t {
                flex: 0 0 45%;
                font-weight: 600;
            }
            .v {
                padding-left: 5px;
            }
        }
    }

    &-cart-content {
        display: none;
        align-items: center;
        background-color: #FFE5E4;
        padding: 16px 20px;
        border-radius: 5px;
        margin-bottom: 10px;
        .btn {
            margin-left: auto;
        }
    }

    &-price {
        font-size: 18px;
        font-weight: 600; 
    }

    &-features-container {
        margin-bottom: 30px;
        .feature-item {
            margin-bottom: 25px;
            .title {
                font-size: 20px;
                font-weight: 500;
                color: #FB0001;
                margin-bottom: 16px;
            }

            .list {
                > .item {
                    display: flex;
                    align-items: center;
                    min-height: 60px;
                    padding: 8px 20px;
                    background-color: #F8F8F8;
                    border-radius: 5px;
                    margin-bottom: 5px;
                    font-size: 12px;
                    .t {
                        font-weight: 600;
                        flex: 0 0 45%;
                    }
                }
            }
        }
    }

    &-documents-container {
        > .title {
            font-size: 20px;
            font-weight: 500;
            color: #FB0001;
            margin-bottom: 16px;
        }
    }

    &-document-list {
        > div {
            margin-bottom: 16px;
        }
        .item {
            a {
                display: flex;
                align-items: center;
                height: 96px;
                border: solid 1px $c-border;
                border-radius: 5px;
                padding: 5px 24px;
                @include transitions();
                &:hover {
                    transform: scale(1.02);
                    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16);
                }

                .icon {
                    margin-right: 20px;
                }
            }
        }
    }
}

.product-compare-swiper {
    overflow-x: auto;
    padding-bottom: 16px;
}

.product-compare-list {
   margin-bottom: 50px;
    > .list {
        display: flex;
        margin: 0 -8px;
        > div {
            flex: 0 0 196px;
            padding: 0 8px;
        }
    }
    
}

.prd-cmp-features {

}

.cmp-features-item {
    margin-bottom: 40px;
    .f-title {
        font-size: 16px;
        font-weight: 500;
        color: #FB0001;
        margin-bottom: 10px;
    }
    .f-row {
        display: flex;
        font-size: 12px;
        &-title {
            flex: 0 0 196px;
            display: flex;
            align-items: center;
            min-height: 55px;
            padding: 10px 0;
            font-weight: 600;
        }
        &-value {
            flex: 0 0 196px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            background-color: #F8F8F8;
            margin-bottom: 10px;
            min-height: 55px;
            padding: 10px;
            text-align: center;
            span {
                display: block;
                color: #FB0001;
                font-size: 10px;
                padding-top: 2px;
            }
        }
    }
}

.no-result-warning{
    padding: 50px 0;
    text-align: center;
    .icon{
        margin-bottom: 10px;
        color: $c-text-dark;
    }
}