.cta-product-item {
    .content {
        padding: 30px 90px;
    }

    > .img {
        picture {
            position: relative;
            height: 425px;
        }
    }

    .cta-product-list {
        margin-top: 40px;

        > * {
            flex: 0 0 144px;
        }
    }
}

#main-slider {
    .banner:not(.bnr-full-img) {
        .bnr-media {
            picture{
                max-width: 630px;
            }
        }
    }
    
    .bnr-title {
        @include font-set(h-large, true);
    }
    .bnr-footer {
        display: flex;
    }
    .bnr-body {
        width: auto;
    }

    .swiper-slide-active{
        .bnr-media {
            picture{
                transform: translateY(0) scale(1);
            }
        }
    }

    .swiper-pagination {
        width: auto !important;
        bottom: 25px;
        left: 33px;
        transform: none;
    }

    .bnr-middle {
        .bnr-body {
            padding: 0;
        }
    }

    .bnr-d-bottom {
        .bnr-media {
            position: absolute;
            top: 0;
            bottom: auto;
            .hotspot-wrapper,
            picture {
                width: auto;
            }
        }
        .bnr-body {
            padding: 0 0 100px 0;
            margin-top: auto;
            height: auto;
        }
    }

    .bnr-d-left,
    .bnr-d-right {
        .bnr-media {
            position: absolute;
            top: 0;
            bottom: auto;
            width: auto;
            height: 100%;
            display: flex;
            align-items: center;
        }
        .bnr-body {
            padding: 0;
            margin-left: auto;
            max-width: 420px;
        }
    }

    .bnr-d-left {
        padding: 60px;
        .bnr-media {
            right: 0;
            left: auto;
            padding-right: 0;
        }
        .bnr-body {
            margin-right: auto;
            margin-left: 0;
            max-width: 420px;
        }
        .hotspot-wrapper {
            left: auto;
        }
    }

    .bnr-d-right {
        padding: 60px;
        .bnr-media {
            right: auto;
            left: 0;
            .hotspot-wrapper {
                right: auto;
            }
        }
    }
}