// header
@include header-appshell;

%header-modals {
    display: none;
    flex-direction: column;
    position: fixed;
    top: -10px;
    left: 0;
    z-index: $z-higher;
    width: 100%;
    height: 100%;
    background: $c-bg-body;
    color: $body-color;
    overflow: auto;
    padding: 0 16px;
    .btn-close {
        position: absolute;
        right: 20px;
        top: 9px;
        z-index: $z-lower;
    }

    &.activated {
        display: block;
    }
}

// menu
#main-menu {
    @extend %header-modals;
    animation: toRight .2s ease;
    padding: 30px 0;
    
    li {
        > a {
            position: relative;
            display: flex;
            align-items: center;
            .qty-badge {
                margin-left: auto;
                margin-right: 45px;
            }
        }
        > .sub {
            display: none;
        }
        &.selected {
            // background: $menu-sub-bg;
            > a {
                font-weight: 700;
            }
            > .sub {
                display: flex;
                flex-direction: column;
            }
        }
        &.all {
            > a {
                &::before {
                    @extend .svg;
                    @extend .svg-plus-c;
                    content: "";
                    margin-right: 10px;
                }
            }
        }
    }
}

.menu-category {
    > li {
        animation: toTop 0.5s cubic-bezier(0.22, 0.61, 0.36, 1) forwards;
        opacity: 0;
        > a {
            padding: $menu-lv-1-padding;
            width: calc(100% - (#{$menu-space-x} * 2));
            margin: 0 auto;
        }
        
        &.selected {
            > a {
                &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 0;
                    transform: translateY(-50%) scale(-1);
                    @extend .svg;
                    @extend .svg-arrow2-r;
                }
            }
        }
        &.no-sub {
            > a {
                &::after {
                    @extend .svg-arrow2-r;
                }
            }
        }
        @for $i from 1 through 10 {
            &:nth-child(#{$i + 1}) {
                animation-delay: $i * .05s;
            }
        }
    }
    // lv 2
    .lv-2 {
        // display: flex;
        // flex-wrap: wrap;
        // padding: 0 10px 20px;
        > li {
            width: calc(100% - (#{$menu-space-x} * 2));
            margin: 0 auto;
            > a {
                // flex-direction: column;
                // text-align: center;
                > .responsive {
                    max-width: $menu-lv-2-img-width;
                }
                &::after {
                    @extend .svg;
                    @extend .svg-plus;
                }
            }
            &.selected {
                > a {
                    &::after {
                        @extend .svg-minus;
                    }
                }
            }
            &.no-sub{
                > a{
                    &::after {
                        @extend .svg-arrow2-r;
                    }
                }
            }
        }
    }
    // lv 3
    .lv-3 {
        display: none;
        padding: 5px 0;
        > li {
            padding: 5px 0;
            > a {
                figure {
                    display: none;
                }
            }
        }
    }
    // banner
    .banner {
        @include responsive(375, 180);
    }
}

.menu-brands {
    $p: 2.5px;
    display: flex;
    flex-wrap: wrap;
    padding: (20px - $p) (10px - $p) (40px - $p);
    > li {
        width: calc(100% / #{$menu-brand-row-item});
        padding: $p;
        > a {
            justify-content: center;
            background: $c-white;
            height: $menu-brand-height;
            @include responsive(115, 61);
            > .responsive {
                max-width: 115px;
            }
        }
    }
}

.menu-links {
    margin-top: 15px;
    padding: 0 $menu-space-x;
    > li {
        > a {
            padding: 15px 0;
        }
    }    
}

.menu-social-links {
    margin-top: 30px;
    padding: 0 $menu-space-x;
    display: flex;
    align-items: center;
    > li {
        margin-right: 30px;
    }
}

// search
$search-space-x: 20px;
#header-search {
    @extend %header-modals;
}

.search-input {
    padding: 30px 0 20px;
}

.suggested-results {
    display: none;
    overflow: auto;
    
    > .list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8px;
        > div {
            flex: 0 0 calc(100% / 2);
            padding: 0 8px;
            margin-bottom: 16px;
        }
    }

    .prd-mini {
        border-color: $c-border;
    }
}

.searching{
    .suggested-results, .search-input .btn-clear{
        display: flex;
    }

    .suggested-searches{
        display: none;
    }
}

// Popper
%popper{
    background-color: $c-light;
    border-radius: $br-popover;
    padding: 20px 15px;
    position: absolute;
    top: $header-height-mobile - 9px;
    width: calc(100% - 20px);
    max-width: 355px;
    color: $body-color;
    display: none;

    &::before{
        content:"";
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 8px 8px 8px;
        border-color: transparent transparent $c-light transparent;
        position: absolute;
        top: -8px;
    }

    &.activated{
        display: block;
    }
}

.ntf{
    &-holder{
        position: relative;
    }
    &-header{
        position: relative;
        margin-bottom: 20px;

        .btn-close{
            position: absolute;
            right: 0;
            top: -10px;

            .icon {
                width: 16px;
                height: 16px;
            }
        }
    }

    &-title{
        @include font-set(h-small);
        color: $c-primary;
    }

    &-item{
        animation: toTop 0.5s cubic-bezier(0.22, 0.61, 0.36, 1) forwards;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        padding: 15px 5px;
        opacity: 0;
        position: relative;

        + .ntf-item{
            border-top: 1px solid $c-grey-light;
        }

        &::before{
            position: absolute;
            top: 20px;
            left: 5px;
        }

        @for $i from 1 through 10 {
            &:nth-child(#{$i + 1}) {
                animation-delay: $i * .05s;
            }
        }

        &.visited {
            .ntf-content {
                opacity: .6;
            }
        }
    }

    @at-root {
        [class*="ntf-type-"] {
            &::before{
                content: "";
                display: inline-block;
                width: 8px;
                height: 8px;
                border-radius: 50%;
            }
        }
    }
    
    @each $i, $ntf in $c-notifiers{
        &-type-#{$i}{
            &::before{
                background-color: $ntf;
            }
        }
    }

    &-no-img{
        padding-left: 23px;
    }

    &-img{
        width: 50px;
        background-color: $c-bg-body;
        border-radius: 50%;
        padding: 5px;
    }

    &-content{
        flex: 1;
        padding-left: 10px;

        b{
            display: block;
            font-weight: $fw-bold;
        }

        p{
            margin-bottom: 0;
        }
    }

    &-detail {
        width: 100%;
        margin-top: 20px;
    }

    &-footer{
        text-align: center;
        margin-top: 15px;
        animation: toTopDelayed 0.5s cubic-bezier(0.22, 0.61, 0.36, 1) forwards;
    }

    &-ready{
        #overlay{
            display: block;
        }
    }

    &-cart-success{
        text-align: center;
        .icon{
            color: $c-success;
        }
    }
    &-msg{
        @include font-set(h-small);
        margin-top: 10px;
    }
}

#cart-notifier{
    @extend %popper;
    z-index: $z-high;
    position: absolute;
    left: auto;
    right: 0;
    top: 50px;
    width: 80vw;
    &::before{
        right: 13px;
    } 

    .ntf-footer{
        display: flex;
        margin-top: 25px;
        .btn{
            padding: 0 20px;
            flex: 1;
            &:first-child{
                margin-right: 10px;
            }
        }
    }
    .wty{
        &-msg{
            text-align: center;
            margin: 5px auto 0;
            max-width: 200px;
        }
        &-row + .wty-row{
            border-top: 1px solid $c-grey-border;
        }
    }
}