// Popup Base
.modal{
    padding:0 !important;
    &-content {
        background-color: #fff;
    }

    &-dialog{
        position: absolute;
        top: 0;
        right: 0;
        margin: 0;
        min-height: 100vh;
        min-height: -webkit-fill-available;
        width: 100%;
    }

    &-header{
        align-items: center;
        padding-bottom: 10px;
        margin: 0 20px;
        justify-content: flex-start;
        border-bottom: 1px solid $c-grey-light;

        &-right{
            margin-left: auto;
            flex: none;
            .close-modal{
                display: inline-block;
                width: 20px;
                height: 20px;
                svg {
                    width: 20px;
                    height: 20px;
                }
            }
        }
        &-icon{
            display: inline-block;
            margin-right: 15px;
        }
    }

    &-title{
        @include font-set(h-xsmall);
    }

    &-body{
        padding: 20px;
        .modal-general-wrapper {
            padding: 20px 0;
            p {
                margin-bottom: 20px;
                font-size: 14px;
            }
        }
    }

    &-footer{
        background-color: $c-light;
        padding: 8px 20px;
        justify-content: center;

        & > *{
            margin: 0;
        }

        .mf-left{
            margin-right: auto;
        }
    }

    &-general {
        .modal-body {
            padding: 0 20px;
            .modal-general-wrapper {
                padding: 0;
            }
        }
        .modal-header {
            border-bottom: none;
        }
    }

    // Modal Themes
    &-light{
        .modal-content{
            background-color: $c-bg-body;
        }
    }
}

.mdl-text-content  {
    .t {
        font-size: 20px;
        font-family: $font2;
        margin-bottom: 10px;
    }
    .v {
        font-size: 14px;
    }
}

// MODAL FILTER
// A FILTER GROUP
.filter-group{
    .fg-clear{
        display:none;
    }

    .fg-more{
        margin-top: 20px;
        display: none;
    }

    // SHOW MORE/LESS
    &.fg-less{
        .fg-body{
            height: 125px;
            overflow: hidden;
        }

        .fg-more{
            display: inline-block;
        }
    }

    &.fg-type-range{
       .rs-holder{
           padding: 16px 20px;
       }

       .range-slider {
            margin-bottom: 30px;
       }

       .range-inputs {
            display: flex;
            margin: 0 -8px;
            > * {
                flex: 0 0 50%;
                max-width: 50%;
                padding: 0 8px;
            }
       }
    }

    &.fg-type-button{
        .btn{
            margin-bottom: 5px;
        }
    }

    &.fg-type-order{
        .fg-body{
            padding-bottom: 20px;
        }
    }

    // WHEN A SELECTION MADE
    &.fg-sel{
        .fg-clear{
            display: inline-block;
            margin-bottom: 20px;
        }

        .fg-body{
            height: auto;
        }

        .fg-more{
            display: none;
        }
    }
}

// A FILTER OPTION
.filter-option{
    padding: 5px;
    display: flex;
    label{
        padding-left: 42px;
    }
    input:checked + label{
        font-weight: $fw-bold;
    }
}

.filter-inputs{
    display: flex;
    padding-bottom: 10px;

    .min{
        margin-right: 10px;
    }
}

.file{
    &-upload{
        padding: 20px 0;
    }
    &-items{
        &-title{
            @include font-set(body-small);
            margin-bottom: 10px;
        }
        &-list{
            margin-bottom: 20px;
        }
    }
    &-item{
        display: flex;
        align-items: center;
        + .file-item{
            margin-top: 10px;
        }
        // Statuses
        &.uploaded, &.uploading{
            .file-button{
                display: none;
            }
            .file-status{
                display: inline-block;
            }
        }
        &.uploaded{
            .file-status{
                @extend .svg;
                @extend .svg-completed;
            }
            .file-thumb{
                display: inline-block !important;
            }
        }
        &.uploading{
            .file-status{
                background: url(#{$dir-images + 'preloader.svg'}) center center no-repeat;
                background-size: contain;
                width: 32px;
                height: 32px;
            }
        }
    }
    &-name{
        flex: 1;
        margin-left: 10px;
    }
    &-thumb{
        width: 40px;
        height: 40px;
        overflow: hidden;
        margin-left: 10px;
        display: none;
    }
    &-status{
        display: none;
    }
    &-select{
        position: relative;
        display: inline-block;
        input{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            z-index: $z-lower;
            cursor: pointer;
            &::-webkit-file-upload-button{
                visibility: hidden;
            }
            &:hover + button{
                background-color: $c-dark;
                color: $c-text-light;
            }
        }
    }
    &-info{
        margin-top: 20px;
        color: $c-text-weak;
    }
}

.otp{
    &-holder{
        padding-top: 30px;
    }
    &-info{
        margin-bottom: 10px;
    }
    &-form{
        .retry{
            padding-top: 10px;
            align-self: center;
        }
        .confirm{
            padding-top: 10px;
            text-align: right;
        }
    }
}

// information message
.msg {
    display: none;
    position: fixed;
    top: 10px;
    left: 50%;
    z-index: $z-ultra;
    transform: translateX(-50%);
    background: #fff;
    padding: 30px 30px 20px;
    width: calc(100% - 20px);
    max-width: 355px;
    border-radius: $br-popup-middle;
    text-align: center;
    .btn-close {
        position: absolute;
        right: 10px;
        top: 10px;

        .icon {
            width: 16px;
            height: 16px;
        }
    }
    .t {
        margin-top: 10px;
        @include font-set(h-small);
    }
    .p {
        margin-top: 5px;
    }
    .b {
        margin-top: 20px;
        .btn + .btn{
            margin-left: 10px;
        }
    }
    &-md {
        max-width: 500px;
    }
    &.activated {
        display: block;
    }
    &-opened {
        #overlay {
            display: block;
            z-index: $z-ultra - 1;
        }
    }
}

.popup-form {
   padding-top: 30px;
   max-width: 310px;
   margin: 0 auto;
}


// Modal Text
.modal-text{
    margin-top: 30px;
    b{
        font-weight: $fw-bold;
    }
}

// Assistant
#modal-assistant{
    .modal{
        &-content{
            background-color: $c-bg-colored;
        }
        &-body{
            padding: 10px 10px 0;
        }
    }
    .banner{
        display: flex;
        flex-direction: column;
    }
    .bnr{
        &-body{
            padding: 00;
            font-size: 0;
        }
        &-media{
            margin: 50px auto 0;
            max-width: 320px;
            mix-blend-mode: darken;
        }
    }
}

.popup {
    display: none;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 20px;
    left: 50%;
    z-index: $z-ultra;
    transform: translateX(-50%);
    background: $c-bg-light;
    padding: 30px 30px 20px;
    width: calc(100% - 40px);
    max-width: 1100px;
    border-radius: $br-popup-middle;
    .btn-close {
        position: absolute;
        right: 10px;
        top: 10px;
        border-radius: 50%;
        background-color: #fff;
    }
    &.activated {
        display: flex;
    }
    &-opened {
        #overlay {
            display: block;
            z-index: $z-ultra - 1;
        }
    }

    &-lg {
        height: calc(100% - 40px);
        max-width: 1000px;
        transform: none;
        inset: 0;
        margin: auto;
    }
}
