.plp-sub-categories {
    margin-bottom: 20px;
    padding-left: calc((100% - 1160px) / 2);
    ul {
        li {
            margin-left: 16px;
            flex: 0 0 calc(100vw / 5.43);
            &:first-child {
                margin-left: 0;
            }
        }
    }
}

.top-panel {
    margin-bottom: 30px;
    
    padding: 16px 25px;
    > .center {
        text-align: center;
        h1 {
            font-size: 22px;
        }
    }
}

.plp-filter-content {
    margin-bottom: 0;
    .btn-list {
        .btn-filter {
            border: none;
        }
    }

    .filter-sorting {
        margin-right: 20px;
        flex: none;
        margin-bottom: 0;
    }
}

.products { 
    margin: 0 -8px;
    > .prd {
        flex: 0 0 calc(100% / 4);
        max-width:calc(100% / 4);
        padding: 0 8px;
        margin-bottom: 16px;
    }
}

body.page-product-detail {
    .top-panel {
        margin-bottom: 16px;
        > .left,.center {
            display: block;
        }
        > .right {
            flex: 0 0 auto;
            display: block;
            margin-left: auto;
        }
    }

    .top-panel-fixed {
        position: static;
        max-width: calc(1160px + 32px);
        background-color: transparent;
        box-shadow: none;
        padding: 0 16px !important;
        .top-panel {
            background-color: $c-light;
        }
        
    }
}

.pdp {
    &-top-shopping {
        display: none;
    }
    &-tab-menu {
        ul {
            display: flex;
            li {
                margin-right: 30px;
                a {
                    &:hover {
                        text-decoration: underline;
                    }
                }
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }

    &-thumb-image {
        display: block;
        margin: 0 -8px;
        .thumb-item {
            padding: 0 8px;
            a {
                position: relative;
                padding-top: 100%;
                display: block;
                border: 1px solid $c-border;
                border-radius: 5px;
                overflow: hidden;
    
                img {
                    position: absolute;
                    left: 0;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    max-width: 100%;
                    max-height: 100%;
                    margin: auto;
                }
            }
    
            &.slick-current  {
                a {
                    border-color: $c-primary;
                }
            }
        }

        .slick-track {
            margin: 0;
        }
    }

    &-labels {
        .prd-txt-label {
            height: 30px;
            font-size: 14px;
        }
    }

    &-options {
        margin: 0 -5px 40px;

        .opt-item {
            width: 82px;
            height: 82px;
        }
    }

    &-features-list {
        margin-bottom: 40px;
        .item {
            .t {
                flex: 0 0 40%;
            }
        }
    }

    &-cart-content {
        display: flex;
    }

    &-features-container {
        margin-bottom: 0;
        .feature-item {
            margin-bottom: 50px;
            .title {
                font-size: 26px;
                margin-bottom: 20px;
            }

            .list {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                > .item {
                    flex: 0 0 calc(50% - 8px);
                    .t {
                        flex: 0 0 40%;
                    }
                }
            }
        }
    }

    &-documents-container {
        > .title {
            font-size: 26px;
            margin-bottom: 20px;
        }
    }

    &-document-list {
        display: flex;
        margin: 0 -8px;
        > div {
            padding: 0 8px;
            flex: 0 0 calc(100% / 3);
        }
    }
}

body.pdp-fixed {
    .pdp-top-shopping {
        display: flex;
    }
    .top-panel-fixed {
        position: sticky;
        position: -webkit-sticky;
        top: 0;
        z-index: 10;
        background-color: $c-light;
        max-width: none;
        width: 100%;
        padding: 0;
    }
}

.product-compare-list {
    display: flex;
    margin-left: auto;
    max-width: 964px;
    > div {
    }
}