.email-cta {
    position: relative;
    background: #fafafa;
    border-radius: 5px;
    box-shadow: 0px 0px 16px rgba(0, 0, 0, 0);
    overflow: hidden;
    margin-bottom: 30px;
    margin-top: 20px;
    a {
        position: relative;
        z-index: 4;
        display: flex;
        flex-direction: column;
        padding: 25px 20px;
        font-size: 18px;

        span {
            display: block;
            &:first-child {
                color: #FB0001;
            }
        }
    }

    img {
        position: absolute;
        left: 20px;
        bottom: -30px;
        max-width: 100px;
    }
}

.faq-container {
    > .title {
        margin-bottom: 30px;
        color: #FB0001;
        font-size: 26px;
        font-weight: 500;
    }

    .acc-item {
        > button {
            padding: 20px;
        }
    }
}

.ntf-item {
    margin-bottom: 16px;
    padding-bottom: 16px;
    border-bottom: solid 1px #707070;
    .ntf-date {
        font-size: 12px;
        margin-bottom: 8px;
    }

    .ntf-title {
        font-weight: 600;
        font-size: 14px;
        font-family: $font2;
        margin-bottom: 10px;
    }

    .ntf-description {
        font-size: 14px;
    }
}