// Software & Guide Finder
.spt-search-ready .sgf-search .spt-search{
    position: fixed;
}

// Main Menu
#main-menu {
    .lv1-li {
        &.selected {
            animation: none;
            opacity: 1;
            > a {
                position: absolute;
                top: 10px;
                left: 0;
                background-color: $c-white;
                padding: 15px 0;
                margin: 0 15px;
                &::after{
                    right: auto;
                    left: 0;
                    transform: translateY(-50%) scale(-1);
                }
                span{
                    margin-left: 32px;
                }
            }
        }
    }
}
.menu-category {
    > li {
        > a {
            display: flex;
            align-items: center;
            background-color: $c-light;
            margin-bottom: 10px;
            border-radius: 5px;
            height: 100px;
            padding-left: 30px;
            padding: 6px 15px 6px 30px;
            figure {
                order: 2;
                width: 80px;
                height: 80px;
                margin-left: auto;
            }
        }

        &.selected {
            > a {
                height: auto;
                figure {
                    display: none;
                }

                &:after {
                    display: block;
                }
            }
            .sub {
                padding-top: 30px;
            }
        }
    }
    .sub{
        > .banner{
            display: none;
        }
    }
    .sublv{
        display: none;
    }
    // lv 2
    .lv-2{
        > li {
            > a {
                display: flex;
                align-items: center;
                background-color: $c-light;
                margin-bottom: 10px;
                border-radius: 5px;
                height: 100px;
                padding-left: 30px;
                padding-right: 15px;
                figure {
                    order: 2;
                    width: 80px;
                    height: 80px;
                    margin-left: auto;
                }
            }

            &.selected{
                .sublv{
                    display: block;
                }
            }
        }
    }
    // lv 3
    .lv-3 {
        display: block;
        padding: 20px 0;
        > li {
            margin-bottom: 10px;
            > a {
                display: block;
            }
        }
    }
    &.lv1-active{
        > li{
            display: none;
            &.selected{
                display: block;
            }
        }
    }
}

#main-slider {
    .bnr-product-list {
        display: flex;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        scroll-snap-type: x mandatory;
        padding-bottom: 10px;
        right: 0;
        left: 10px;
        top: auto;
        bottom: 30px;
        max-width: none;
        > * {
            flex: 0 0 80vw;
            margin-right: 16px;
            margin-bottom: 0;
            scroll-snap-align: start;
        }
    }
}

.quick-account-list  {
    .row {
        flex-wrap: nowrap;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        scroll-snap-type: x mandatory;
        padding-bottom: 1rem;
        .col-lg-4 {
            max-width: 85vw;
            flex: 0 0 85vw;
            scroll-snap-align: start;
        }
    }

    .account-menu-item {
        margin-bottom: 0;
    }
}

[data-swiper="productSwiper"] {
    overflow: auto;
    
    > .swiper-wrapper {
        flex-wrap: wrap;
        margin: 0 -5px;
        width: auto;
        > .swiper-slide {
            margin-bottom: 10px;
            margin-right: 0 !important;
            padding: 0 5px;
            flex: 0 0 50%;
            max-width: 50%;
        }
    }
}

.prd {
    &-txt-label {
        display: none;
    }

    &-name {
        margin-bottom: 5px;
    }

    &-price {
        display: flex;
        align-items: center;
    }
}

.modal {
    height: calc(100% - 60px);
    bottom: 0;
    top: auto;
    &-header {
        display: none;
    }

    &.fade {
        .modal-dialog {
            transform: translateY(calc(100% + 50px));
        }
    }
    &.show {
        .modal-dialog {
            transform: none;
        }
    }
}
[class*="-ready"]:not(.ajx-drp-ready):not(.ntf-ready),
body.modal-open {
    .m-close-modal {
        display: block;
    }

    .header-top {
        z-index: 21;
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
        border-radius: 5px;

        #nav-user-menu {
            animation: hideElement 0.2s forwards;
            animation-delay: 0.2s;
        }
    }
}

[class*="-ready"]:not(.ajx-drp-ready):not(.ntf-ready) {
    .m-close-ovg {
        display: block;
    }
}

#main-menu, #header-search {
    height: calc(100% - 60px);
    bottom: 0;
    top: auto;
    transition: transform 0.3s ease-out;
    transform: translate(calc(100% + 50px));

    &.activated {
        transform: none;
    }
}

.m-close-ovg,
.m-close-modal {
    position: absolute;
    width: 16px;
    height: 16px;
    top: 26px;
    right: 20px;
    z-index: 22;
    opacity: 0;
    animation: showElement 0.4s forwards;
    animation-delay: 0.4s;
    svg {
        width: 16px;
        height: 16px;
    }
}

@keyframes hideElement {
    to {
      display: none;
    }
}

@keyframes showElement {
    to {
        opacity: 1;
    }
}