    // Category Swiper
#category-swiper{
    .swiper-container{
        padding: 0 40px;
    }

    .swiper-scrollbar{
        width: calc(100% - 80px);
        display: block;
        max-width: 1520px;
    }

    .swiper-slide{
        width: 190px;
        height: 205px;
        padding: 0 10px;
    }
    &.c-aligned{
        .swiper-wrapper{
            justify-content: center;
        }
    }
}

.page-home{
    #category-swiper{
        @include cont-aligner(1600px);
    }
}

