

.prd {
    &-row {
        text-align: left;
        background-color: #fff;
        border-radius: 5px;
        @include transition();
        a {
            display: flex;
            align-items: center;
        }
        &-img {
            flex: 0 0 30%;
        }
        &-content {
            flex: auto;
            padding: 10px 20px;
        }
        
        .prd-brand {
            margin-bottom: 8px;
        }

        &:hover {
            transform: scale(1.02);
            box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16);
        }
    }

    &-mini {
        background-color: #fff;
        border-radius: 5px;
        border: solid 1px transparent;
        @include transition();
        &-img {
            width: 70%;
            margin: auto;
        }
        
        &-content {
            padding: 10px;
        }

        .prd-title {
            font-size: 14px;
        }

        .prd-price {
            font-size: 12px;
            .prc-first {
                font-size: 12px;
            }
        }
        
        .prd-brand {
            margin-bottom: 8px;
        }

        &:hover {
            transform: scale(1.02);
            box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16);
        }
    }

    &-inner {
        position: relative;
        border: solid 1px $c-border;
        border-radius: 5px;
        overflow: hidden;
        @include transition();
        &:hover {
            transform: scale(1.02);
            box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16);
        }
    }

    &-label-container {
        > * {
            position: absolute;
            z-index: 4;
        }

        .prd-brand {
            left: 10px;
            top: 10px;
        }

        .prd-energy {
            left: 10px;
            bottom: 10px;
            max-width: 40px;
        }

        .prd-compare {
            right: 0;
            top: 5px;
        }

        .prd-txt-label {
            right: 10px;
            bottom: 10px;
        }
    }

    &-media {
        position: relative;
        .swiper-slide {
            background-color: $c-light;
        }
        .swiper-button-prev, .swiper-button-next{
            display: none;
        }

        .swiper-pagination-bullets {
            position: absolute;
            z-index: 4;
            bottom: 10px;
        }
    }

    &-body {
        padding: 15px 10px 10px;
    }

    &-txt-label {
        height: 20px;
        display: flex;
        text-align: center;
        align-items: center;
        padding: 0 10px;
        color: $c-brand;
        font-size: 10px;
        background-color: #fff;
        border-radius: 5px;
        border: solid 1px transparent;
        &.accent {
            border-color: #FB0001;
            color: #FB0001;
        }
    }

    &-brand {
        width: 60px;
        height: 20px;
        background-color: $c-brand;
        border-radius: 3px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 3px;
        &.brand-arcelik {
            background-color: $c-arcelik;
        }
        &.brand-altus {
            background-color: $c-altus;
        }
    }

    &-title {
        font-family: $font2;
        font-size: 16px;
        font-weight: 600;
        height: 40px;
        line-height: 20px;
        @include line-clamp(2);
    }

    &-name {
        font-family: $font2;
        font-size: 16px;
        font-weight: 400;
        .t,
        .v {
            height: 20px;
            line-height: 20px;
            @include line-clamp(1);
        }
    }

    &-price {
        font-size: 16px;
        .prc-first {
            margin-right: 5px;
            font-size: 14px;
        }
    }

    &-compare {
        display: flex;
        align-items: center;
        user-select: none;
        cursor: pointer;

        input {
            @extend .svg;
            @extend .svg-compare;
            appearance: none;
            cursor: pointer;
            border: none;
            background-color: transparent;
            
            &:not(:checked) + label {
                .t1 {
                    display: none;
                }
            }
            &:checked + label {
                .t0 {
                    display: none;
                }
            } 
        }
        label {
            @include font-set(body-small);
            margin: 0;
            padding: 0 0 0 5px;
            cursor: pointer;

            .t1 {
                padding-right: 10px;
            }
        }
    }

    &-default {
        .prd-name {
            flex: 0 0 70%;
        }
        .prd-price {
            flex: 1;
            .prc-first,
            .prc-last {
                display: block;
            }
        }
    }
}

.prd-list-cmp {
    border: solid 1px #D0D0D0;
    border-radius: 5px;
    overflow: hidden;
    .btn-cmp-remove {
        position: absolute;
        z-index: 3;
        right: 10px;
        top: 10px;
        .icon {
            width: 16px;
            height: 16px;
        }
    }
    .cmp-image {
        background-color: $c-light;
        padding-top: 100%;
        position: relative;
        img {
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;
            max-width: 100%;
            max-height: 100%;
        }
    }

    .cmp-content {
        padding: 10px;
        text-align: center;
        .cmp-title {
            font-family: $font2;
            font-size: 16px;
            font-weight: 600;
            height: 40px;
            line-height: 20px;
            @include line-clamp(2);
            margin-bottom: 10px;
        }

        .cmp-price {
            font-size: 16px;
        }
    }

    .cmp-footer {
        padding: 0 10px 10px;
        .btn {
            width: 100%;
        }
    }
}

.cmp-add-item {
    height: 100%;
    border: solid 1px #D0D0D0;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: 600;
    .icon {
        width: 81px;
        height: 81px;
        margin: 0 auto 12px;
        display: block;
        opacity: .1;
    }
}

.product-list-container {
    margin-bottom: 50px;

    .swiper-button-next,.swiper-button-prev {
        display: none;
    }
}
