#site-footer {
    margin-top: 30px;
}

.footer-top {
    background-color: #DBDBDB;
    padding: 50px 0 20px;
}

.footer-menu {
    &-item {
        margin-bottom: 30px;
        .title {
            font-size: 16px;
            font-weight: 600;
            font-family: $font2;
            margin-bottom: 10px;
        }
        ul {
            li {
                margin-bottom: 10px;
                font-size: 14px;
                a {
                    color: $c-dark;
                    @include transition();
                    &:hover {
                        color: $c-brand;
                    }
                }
            }
        }
    }
}

.footer-bottom {
    padding: 20px 0;
}

.footer-logo {
    text-align: center;
    margin-bottom: 10px;
    img {
        max-height: 22px;
    }
}

.footer-contact {
    font-size: 14px;
    text-align: center;
    margin-bottom: 10px;
    span {
        color: $c-brand;
    }
    a {
        color: $c-dark;
        @include transition();
        &:hover {
            color: $c-brand;
        }
    }
}

.footer-quick-menu {
    text-align: center;
    ul {
        display: flex;
        justify-content: center;
        margin: 0 8px;
        li {
            padding: 0 8px;
            font-size: 14px;
            a {
                color: $c-dark;
                @include transition();
                &:hover {
                    color: $c-brand;
                }
            }
        }
    }
}