.ord-header {
    display: flex;
    align-items: center;

    .ord-search {
        flex: 0 0 230px;
    }
}

.ord-accordion {
    .acc-item {
        > .item {
            flex: 1;
        }
    }

    .acc-body {
        display: flex;
        flex-wrap: wrap;
        .left {
            flex: 0 0 350px;
        }
        .right {
            margin-left: auto;
            margin-top: 0;
        }
    }
}

.purchase-accordion {
    display: flex;
    margin: 0 -8px;
    > li {
        flex: 0 0 50%;
        margin: 0 8px;
    }
    
    .usr-ord-list  {
        max-width: 60%;
    }
}